import { uniSwiperDot } from "@dcloudio/uni-ui";
export default {
    data() {
        return {
            isdisplay: 'none',
            count: 1,
            isdisabled: false,
            chooseList: [
                {
                    title: '套餐分类',
                    chooseItem: [
                        {
                            id: 1,
                            name: '官方标配'
                        }
                    ]
                },
                {
                    title: '规格',
                    chooseItem: [
                        {
                            id: 2,
                            name: '180*20cm'
                        },
                        {
                            id: 3,
                            name: '190*20cm'
                        }
                    ]
                }
            ],
            choosed: [],
            choosedName: '',
            info: [{
                content: '内容 A'
            }, {
                content: '内容 B'
            }, {
                content: '内容 C'
            }],
            current: 0,
            mode: 'long',
            dotsStyles: {
                backgroundColor: "rgba(253, 208, 0, 0.3)",
                border: "1px rgba(253, 208, 0, 0.3) solid",
                color: "#fff",
                selectedBackgroundColor: "rgba(253, 208, 0, 0.7)",
                selectedBorder: "1px rgba(253, 208, 0, 0.7) solid"
            }
        }
    },
    methods: {
        change(e) {
            this.current = e.detail.current;
        },
        open() {
            if(this.isdisplay == 'none') {
                this.isdisplay = 'block';
            }
        },
        close() {
            if(this.isdisplay == 'block') {
                this.isdisplay = 'none';
            }
        },
        sub() {
            if(this.count == 1) {
                this.isdisabled = true;
            }else {
                this.count--;
            }
            console.log(this.isdisabled);
        },
        add() {
            this.count++;
            if(this.count > 1) {
                this.isdisabled = false;
            }
        },
        numChange(e) {
            console.log(e);
            let count = e.detail.value;
            if(!(/^[0-9]*[1-9][0-9]*$/).test(count)) {
                uni.showToast({
                    title: '只能输入正整数，请重新输入',
                    icon: 'none'
                });
            }else {
                this.count = count;
                console.log(this.count);
            }
            
        },
        choose(index,key) {
            let array = this.chooseList[index]['chooseItem'][key];
            console.log(index);
            if(array['active']) {
                array['active'] = false;
                var index = this.choosed.indexOf(array.id);
                this.choosed.splice(index,1);
            }else {
                array['active'] = true;
                this.choosed.push(array.id);
            }
            this.chooseList = JSON.parse(JSON.stringify(this.chooseList));
            if(!(array.id > 1)) {
                array.active == true ? this.choosedName = array.name : this.choosedName = '';
            }
        }
    },
    components: {uniSwiperDot},
    onLoad() {
        
    }
}
